<template lang="pug">
  van-cell-group.user-list(v-if="users.length > 0" inset title="快捷选择")
    .item(
      v-for="user in users"
      :key="user.id"
      @click="changeUserid(user)"
      :class="{ active: user === activeUser }"
    ) {{ user.username }}
</template>

<script>
export default {
  data() {
    return {
      activeUser: null
    }
  },
  computed: {
    users() {
      try {
        return JSON.parse(localStorage.getItem('config')).users
      } catch (e) {
        console.warn('未配置用户')
      }
      return []
    }
  },
  mounted() {
    if (this.users.length > 0) {
      this.changeUserid(this.users[0])
    }
  },
  methods: {
    changeUserid(user) {
      this.activeUser = user
      this.$emit('change', user)
    }
  }
}
</script>

<style scoped lang="scss">
  .user-list {
    display: flex;
    padding: 15px;
    overflow: auto;
    .item {
      display: flex;
      align-items: center;
      background: #e8e8e8;
      padding: 8px 16px;
      border-radius: 5px;
      color: #233;
      margin-right: 10px;
      border: 1px solid #e8e8e8;
      &.active {
        border-color: #1989fa;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
</style>
